@use "global" as *;

.p-realization2__wrapper {
    padding-top: rem(240);
    padding-bottom: rem(56);

    @include mq("md") {
        padding-top: rem(140);
        padding-bottom: rem(113);
    }
}

.p-realization2__items {
    margin-top: rem(43);

    @include mq("md") {
        margin-top: rem(95);
    }
}

.p-realization2__item {
    &+& {
        margin-top: rem(78);

        @include mq("md") {
            margin-top: rem(163);
        }
    }
}

.p-scene {
    background-image: linear-gradient(#fff 0% 17%, #F2F4F1 17% 89%, #fff 89% 100%);
}

.p-scene__inner {
    @include mq("md") {
        width: 82%;
        margin: 0 auto;
    }
}

.p-scene__head {
    position: relative;
    padding-inline: rem(16);

    @include mq("xl") {
        padding: 0;
    }
}

.p-scene__label {
    position: absolute;
    top: rem(-7);
    left: rem(28);
    font-family: $second-font-family;
    font-weight: $semi-bold;
    letter-spacing: rem(0.64);

    @include mq("md") {
        font-size: rem(40);
        letter-spacing: rem(1.6);
        top: rem(-20);
        left: rem(40);
    }
}

.p-scene__body {
    @include mq("xl") {
        display: flex;
        margin-top: rem(48);

        &.--reverse {
            flex-direction: row-reverse;
        }
    }
}

.p-scene__content {
    margin: rem(24) rem(16) 0;
    padding: 0 rem(16) rem(32);
    box-shadow: 0 rem(4) rem(4) 0 rgba(#000, 0.16);
    background: $color-base;

    @include mq("xl") {
        padding: rem(64);
        margin: 0;
        width: 96%;
    }
}

.p-scene__lead {
    font-size: rem(24);
    font-weight: $bold;
    line-height: 2.1;
    letter-spacing: rem(0.96);
    color: $color-base;
    background: $color-accent02;
    padding: rem(4) rem(8);
    display: inline;
    box-decoration-break: clone;
}

.p-scene__point-block {
    margin-top: rem(37);

    &+& {
        margin-top: rem(51);
    }

    @include mq("xl") {
        width: 93%;
        margin-top: rem(35);
    }
}

.p-scene__point {
    font-family: $second-font-family;
    font-size: rem(20);
    font-weight: $semi-bold;
    letter-spacing: rem(0.8);
    text-transform: uppercase;
    display: flex;
    align-items: center;
    gap: rem(24);

    &::after {
        content: "";
        background: $color-font;
        height: rem(1);
        flex-grow: 1;
    }
}

.p-scene__text {
    margin-top: rem(22);
    line-height: 1.5;
    letter-spacing: rem(0.64);
}

.p-scene__image {
    margin-top: rem(16);

    @include mq("xl") {
        display: none;
    }
}

.p-scene__foot {
    margin-top: rem(84);
}

.p-scene__title-block {
    width: 92%;
    margin: 0 auto;

    @include mq("xl") {
        width: 84%;
    }
}

.p-scene__title {
    font-size: rem(20);
    font-weight: $bold;
    letter-spacing: rem(0.8);
    position: relative;
    text-align: center;

    &::before,
    &::after {
        position: absolute;
        content: "";
        top: 50%;
        transform: translateY(-50%);
        height: rem(1);
        width: rem(33);
        background: $color-font;

        @include mq("md") {
            width: rem(240);
        }
    }

    &::before {
        left: 0;
    }

    &::after {
        right: 0;
    }
}

.p-scene__sub-title {
    color: #B1B6AF;
    font-family: $second-font-family;
    font-size: rem(20);
    font-weight: $semi-bold;
    letter-spacing: rem(0.8);
    text-transform: uppercase;
    margin-top: rem(18);
    text-align: center;

    @include mq("xl") {
        margin-top: rem(20);
    }
}

.p-scene__items {
    margin-top: rem(30);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: rem(49);

    @include mq("md") {
        margin-top: rem(28);
        flex-direction: row;
        justify-content: center;
    }
}

.p-scene-card {
    width: rem(300);
    background: $color-main;
    border-radius: 0 0 rem(8) rem(8);
    box-shadow: 0 rem(4) rem(4) 0 rgba(#000, 0.16);

    &:hover {
        cursor: pointer;
    }
}

.p-scene-card__body {
    padding: rem(30) 0 rem(67) rem(24);
}

.p-scene-card__name {
    font-size: rem(20);
    font-weight: $bold;
    letter-spacing: rem(0.8);
}

.p-scene-card__detail {
    font-family: $second-font-family;
    font-weight: $semi-bold;
    letter-spacing: rem(0.64);
    margin-top: rem(16);
}

.p-scene__image-block {
    display: none;

    @include mq("xl") {
        margin-top: rem(39);
        margin-bottom: rem(52);
        display: flex;
        gap: rem(27);
    }
}

.p-scene__image--pc1 {
    margin-top: auto;
    margin-left: rem(-52);
}

.p-scene__image--pc3 {
    margin-top: auto;
}

.p-scene__image--pc4 {
    margin-right: rem(-52);
    z-index: 2;
}
