@use "global" as *;

.p-faq__wrapper {
    padding-block: rem(56);

    @include mq("md") {
        padding-block: rem(88);
    }
}

.p-faq__boxes {
    margin-top: rem(40);

    @include mq("xl") {
        margin-top: rem(75);
        display: flex;
        flex-wrap: wrap;
        gap: rem(24);
    }
}

.p-faq-box {
    &+& {
        margin-top: rem(26);
    }

    @include mq("xl") {
        width: 48%;

        &+& {
            margin-top: 0;
        }
    }
}

.p-faq-box__head {
    display: flex;
    align-items: center;
    border-bottom: 1px solid $color-font;
    gap: rem(24);
    padding-bottom: rem(24);

    &::before {
        content: "Q";
        width: rem(14);
        height: rem(22);
        color: $color-accent02;
    }

    &.is-open {
        border: none;

        .p-faq-box__head-text {
            &::after {
                transform: translateY(-50%) rotate(0);
            }
        }
    }
}

.p-faq-box__head-text {
    line-height: 1.5;
    letter-spacing: rem(0.64);
    position: relative;
    flex: 0 1 100%;
    padding-right: rem(35);

    &::before {
        display: block;
        position: absolute;
        content: "";
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        width: rem(16);
        height: rem(2);
        background: $color-accent02;
    }

    &::after {
        display: block;
        position: absolute;
        content: "";
        top: 50%;
        right: 0;
        transform: translateY(-50%) rotate(90deg);
        width: rem(16);
        height: rem(2);
        background: $color-accent02;
        transition: transform 0.3s;
    }
}

.p-faq-box__body {
    display: none;
}

.p-faq-box__body-text {
    position: relative;
    padding-left: rem(24);
    line-height: 1.5;
    letter-spacing: rem(0.64);

    &::after {
        position: absolute;
        content: "A";
        top: 0;
        left: 0;
        width: rem(14);
        height: rem(22);
        color: $color-accent02;
    }
}

.p-faq__button-wrapper {
    margin-top: rem(35);
    text-align: center;

    @include mq("md") {
        margin-top: rem(75);
    }
}

.p-faq__button {
    font-weight: $bold;
    letter-spacing: rem(0.64);
    color: $color-base;
    background: $color-accent01;
    padding: rem(24) rem(76);
    border-radius: rem(30);
    position: relative;
    transition: opacity 0.3s;

    &::after {
        display: block;
        position: absolute;
        content: "";
        top: 50%;
        right: rem(20);
        transform: translateY(-50%);
        width: rem(11);
        height: rem(15);
        background: url(../images/icon-triangle.png) no-repeat center center / contain;
    }

    &:hover {
        opacity: 0.56;
    }
}
