@use "global" as *;

.p-use__wrapper {
    padding-block: rem(56);

    @include mq("md") {
        padding-block: rem(88);
    }
}

.p-use__items {
    margin-top: rem(35);

    @include mq("md") {
        margin-top: rem(75);
        display: flex;
        gap: rem(36);
    }
}

.p-use__item {
    max-width: rem(343);
    margin: 0 auto;

    &+& {
        margin-top: rem(32);
    }

    @include mq("md") {
        flex: 1;

        &+& {
            margin-top: 0;
        }
    }
}

.p-use-step__lead {
    margin-top: rem(29);
    text-align: center;
    font-size: rem(20);
    font-weight: $bold;
    letter-spacing: rem(0.8);
}

.p-use-step__number {
    color: $color-accent01;
    font-size: rem(24);
    font-weight: $bold;
    margin-right: rem(24);
    font-family: $second-font-family;
}

.p-use-step__text {
    line-height: 1.5;
    letter-spacing: rem(0.64);
    margin-top: rem(16);
}
