@use "global" as *;

.p-feature {
    background: url(../images/feature-bg.jpg) no-repeat center center / cover;
}

.p-feature__wrapper {
    padding-block: rem(56);

    @include mq("xl") {
        padding: rem(80) rem(130);
    }
}

.p-feature__items {
    margin-top: rem(58);
}

.p-feature__item {
    margin: 0 auto;

    &+& {
        margin-top: rem(32);
    }
}

.p-feature-media {
    background: $color-base;
    padding: rem(32) rem(16);
    border-radius: rem(16);
    box-shadow: 0 rem(4) rem(4) 0 rgba(#000, 0.16);

    @include mq("xl") {
        max-width: rem(1180);
        width: 100%;
        padding-inline: rem(106);
        display: flex;
        gap: rem(80);
        align-items: center;

        &:nth-child(2n) {
            flex-direction: row-reverse;
        }
    }
}

.p-feature-media__image {
    position: relative;
}

.p-feature-media__content {
    margin-top: rem(24);

    @include mq("xl") {
        width: 93%;
        margin-top: 0;
    }
}

.p-feature-media__label {
    font-family: $second-font-family;
    font-size: rem(20);
    font-weight: $semi-bold;
    letter-spacing: rem(0.8);
    color: $color-base;
    background: $color-accent02;
    padding: rem(9) rem(16);
    display: inline-block;
}

.p-feature-media__lead {
    font-size: rem(20);
    font-weight: $bold;
    line-height: 1.5;
    letter-spacing: rem(0.8);
    margin-top: rem(12);
}

.p-feature-media__letter {
    color: $color-accent02;
    font-size: rem(24);
    letter-spacing: rem(0.96);
}

.p-feature-media__text {
    line-height: 1.5;
    letter-spacing: rem(0.64);
    margin-top: rem(12);
}
