@use "global" as *;

.l-cta {
    background: url(../images/cta.png)no-repeat top / cover;
    padding: rem(18) rem(16);

    @include mq("md") {
        padding-block: rem(53);
    }
}

.l-cta__content {
    text-align: center;
    position: relative;

    &::before,
    &::after {
        position: absolute;
        content: "";
        top: 50%;
        transform: translateY(-50%);
        width: rem(56);
        height: rem(74);
    }

    &::before {
        left: 0;
        background: url(../images/leaf-left.png) no-repeat center center / contain;
    }

    &::after {
        right: 0;
        background: url(../images/leaf-right.png) no-repeat center center / contain;
    }

    @include mq("md") {
        max-width: rem(758);
        margin: 0 auto;

        &::before,
        &::after {
            width: rem(158);
            height: rem(203);
        }
    }
}

.l-cta__text {
    font-weight: $bold;
    line-height: 1.5;
    letter-spacing: rem(0.64);

    @include mq("md") {
        font-size: rem(24);
        letter-spacing: rem(0.96);
    }
}

.l-cta__lead {
    font-size: rem(32);
    font-weight: $bold;
    letter-spacing: rem(1.28);

    @include mq("md") {
        font-size: rem(64);
        letter-spacing: rem(2.56);
        line-height: 1.5;
    }
}
