@use "global" as *;

.p-realization__wrapper {
    padding-block: rem(88);
}

.p-realization__items {
    margin-top: rem(70);
    display: flex;
    flex-direction: column;
    align-items: center;

    @include mq("md") {
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-start;
    }
}

.p-realization__item {
    text-align: center;
    width: rem(168);

    &+& {
        margin-top: rem(32);

        @include mq("md") {
            margin-top: 0;
        }
    }
}

.p-realization__label {
    font-family: $second-font-family;
    font-size: rem(20);
    font-weight: $semi-bold;
    letter-spacing: rem(0.8);
    color: $color-base;
    background: $color-accent02;
    padding: rem(9) rem(24);
    display: inline-block;
}

.p-realization__image {
    img {
        width: rem(168);
    }
}

.p-realization__text {
    line-height: 1.5;
    letter-spacing: rem(0.64);
}
