@use "global" as *;

.p-solution {
    background: url(../images/solution.jpg) no-repeat center center / cover;
    position: relative;
}

.p-solution__wrapper {
    padding-top: rem(56);
    padding-bottom: rem(112);

    @include mq("md") {
        padding-top: rem(88);
    }
}

.p-solution__boxes {
    margin-top: rem(38);

    @include mq("md") {
        margin-top: rem(70);
    }
}

.p-solution-box {
    background: $color-base;
    border-radius: rem(32);
    padding: rem(28) rem(50);
    display: flex;
    flex-direction: column;
    gap: rem(16);

    &+& {
        margin-top: rem(24);
    }

    &:last-child {
        margin-top: rem(24);
    }

    @include mq("md") {
        flex-direction: row;
        gap: rem(32);
    }
}

.p-solution-box__wrapper {
    margin-top: rem(24);
    display: flex;
    flex-direction: column;

    @include mq("xl") {
        flex-direction: row;
        gap: rem(24);

        .p-solution-box {
            margin-top: 0;
            flex: 1;
        }
    }
}

.p-solution-box__icon {
    width: rem(32);
    height: rem(32);
}

.p-solution-box__text {
    font-size: rem(20);
    line-height: 1.5;
    letter-spacing: rem(0.8);
}

.p-solution__lead-block {
    width: 100%;
    max-width: rem(343);
    bottom: rem(-123);
    position: absolute;
    left: 50%;
    transform: translateX(-50%);

    @include mq("md") {
        max-width: rem(1180);
        bottom: rem(-75);
    }
}

.p-solution__lead-line {
    border: 1px solid $color-font;
    height: rem(155);
    width: 100%;
    position: absolute;
    top: rem(13);

    &::before {
        display: block;
        position: absolute;
        content: "";
        bottom: rem(-1);
        left: 50%;
        transform: translateX(-50%);
        background-color: $color-accent01;
        height: rem(1);
        width: rem(20);
    }

    &::after {
        display: block;
        position: absolute;
        content: "";
        bottom: rem(-43);
        left: calc(50% - rem(35));
        width: rem(85);
        height: rem(1);
        background-color: $color-font;
        transform: rotate(95deg);
    }

    @include mq("md") {
        height: rem(78);

        &::before {
            width: rem(96);
        }

        &::after {
            transform: rotate(120deg);
            width: rem(50);
            bottom: rem(-22);
            left: calc(50% + rem(12));
        }
    }
}

.p-solution__lead {
    font-size: rem(20);
    font-weight: $bold;
    line-height: 1.5;
    letter-spacing: rem(0.8);
    color: $color-base;
    background: $color-accent01;
    padding: rem(30) rem(23);
    width: 100%;
    max-width: rem(308);
    margin: 0 auto;
    text-align: center;

    @include mq("md") {
        max-width: rem(1115);
        font-size: rem(24);
    }
}
