@use "global" as *;

.l-footer {
    color: $color-base;
    background: $color-font;
    cursor: pointer;
}

.l-footer__inner {
    padding: rem(38) rem(16);

    @include mq("md") {
        padding: rem(56) rem(130) rem(32);
    }
}

.l-footer-nav__items {
    @include mq("md") {
        display: flex;
        justify-content: space-between;
    }
}

.l-footer-nav__item {
    &+& {
        margin-top: rem(58);

        @include mq("md") {
            margin-top: 0;
        }
    }
}

.l-footer-nav__title {
    font-family: $second-font-family;
    font-size: rem(20);
    font-weight: $semi-bold;
    letter-spacing: rem(0.8);
    text-transform: uppercase;
    position: relative;

    &::after {
        position: absolute;
        content: "";
        bottom: rem(-12);
        left: 0;
        width: rem(72);
        height: rem(1);
        background: $color-base;
    }
}

.l-footer-nav__sub-items {
    margin-top: rem(32);
}

.l-footer-nav__sub-item {
    letter-spacing: rem(0.64);

    &+& {
        margin-top: rem(16);
    }
}

.l-footer-sns__items {
    margin-top: rem(76);
    display: flex;
    gap: rem(20);
}

.l-footer-sns__link {
    img {
        width: rem(32);
        height: rem(32);
    }
}

.l-footer__text-block {
    margin-top: rem(72);
    display: flex;
    flex-direction: column;
    gap: rem(16);
}

.l-footer__text {
    font-size: rem(12);
    font-weight: $bold;
    line-height: 1.5;
    letter-spacing: rem(0.48);
}

.l-footer__copyright {
    font-size: rem(19);
    letter-spacing: rem(0.4);
}
