@use "global" as *;

.l-header {
    position: relative;
    z-index: 2;
    background: $color-base;
    height: rem(48);

    &:hover {
        cursor: pointer;
    }

    @include mq("md") {
        height: rem(70);
    }
}

.l-header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: rem(12) rem(16);

    @include mq("md") {
        padding-inline: rem(130);
    }
}

.l-header__logo {
    img {
        width: rem(52);

        @include mq("md") {
            width: rem(113);
        }
    }
}

.l-header__content {
    display: flex;
    gap: rem(71);
    align-items: center;
    margin-top: rem(-7);

    @include mq("xl") {
        gap: rem(42);
    }
}

.l-header__items {
    display: flex;
    align-items: center;
    gap: rem(36);

    @include mq("md") {
        gap: rem(56);
    }
}

.l-header__heading {
    position: relative;
    font-size: rem(14);
    font-weight: $bold;
    letter-spacing: rem(0.56);
    transition: all 0.3s;

    @include mq("md") {
        font-size: rem(16);
        letter-spacing: rem(0.64);
    }

    &::after {
        position: absolute;
        content: "";
        display: block;
        top: 50%;
        right: rem(-23);
        transform: translateY(-50%);
        width: rem(16);
        height: rem(16);
        background: url(../images/triangle-down.png) no-repeat center center / contain;
        transition: all 0.3s;
    }

    &.is-open {
        color: #B1B6AF;

        &::after {
            position: absolute;
            content: "";
            display: block;
            top: 50%;
            right: rem(-18);
            transform: translateY(-50%);
            width: rem(16);
            height: rem(16);
            background: url(../images/triangle-up.png) no-repeat center center / contain;
        }
    }
}

.l-header__button-wrapper {
    display: flex;
    gap: rem(10);
    align-items: center;

    @include mq("md") {
        gap: rem(40);
    }
}

.l-header__button {
    img {
        width: rem(24);
    }

    @include mq("md") {
        display: none;
    }
}

.l-header__cart {
    img {
        width: rem(24);

        @include mq("md") {
            width: rem(32);
        }
    }
}

.l-header__corporation {
    display: none;
    font-weight: $bold;
    font-size: rem(16);
    letter-spacing: rem(0.64);

    @include mq("md") {
        display: block;
    }
}

.l-header__button-pc {
    display: none;
    font-size: rem(14);
    font-weight: $bold;
    letter-spacing: rem(0.56);
    border: 1px solid $color-font;
    border-radius: rem(25);
    padding: rem(12) rem(24);
    color: $color-font;
    transition: all 0.3s;

    @include mq("md") {
        display: block;
    }

    &:hover {
        color: $color-base;
        background: $color-font;
    }
}
