@use "global" as *;

body {
    font-family: $base-font-family;
    color: $color-font;
    font-size: rem(16);
    font-weight: $normal;
    line-height: 1;
    background: $color-base;
    position: relative;
}

.l-main__inner {
    padding-inline: rem(16);

    @include mq("md") {
        padding-inline: rem(130);
    }
}
