@use "global" as *;

.l-menu {
    padding-inline: rem(16);
    position: relative;
}

.l-menu__content {
    display: none;
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 2;
    background: $color-base;
    width: 92%;
    border-radius: rem(8);
    margin-top: rem(8);

    &.is-open {
        display: block;
    }

    &:hover {
        display: block;
    }

    @include mq("md") {
        top: 100%;
        width: 82%;
        max-width: rem(1180);
        margin-top: rem(17);
    }
}

.l-menu__inner {
    padding: rem(16);

    @include mq("md") {
        padding: rem(24) rem(40);
    }
}

.l-menu__title {
    font-weight: $bold;
    letter-spacing: rem(0.64);

    @include mq("md") {
        font-size: rem(20);
        letter-spacing: rem(0.8);
    }
}

.l-menu__items {
    margin-top: rem(24);
    display: flex;
    flex-wrap: wrap;
}

.l-menu__item {
    font-size: rem(14);
    font-weight: $bold;
    letter-spacing: rem(0.56);
    border-bottom: 2px solid $color-main;
    padding-bottom: rem(8);
    width: 46%;
    margin-top: rem(32);

    &:nth-child(2n) {
        margin-left: rem(24);
    }

    &:nth-child(-n + 2) {
        margin-top: 0;
    }

    @include mq("md") {
        font-size: rem(16);
        letter-spacing: rem(0.64);
        width: rem(192);
        margin: rem(48) rem(32) 0 0;
        transition: all 0.3s;

        &:hover {
            border-bottom: 2px solid $color-accent01;
        }

        &:nth-child(2n) {
            margin-left: 0;
        }

        &:nth-child(4n) {
            margin-right: 0;
        }

        &:nth-child(-n + 4) {
            margin-top: 0;
        }
    }

    @include mq("xl") {
        &:nth-child(4n) {
            margin-right: rem(32);
        }

        &:nth-child(5n) {
            margin-right: 0;
        }

        &:nth-child(-n + 5) {
            margin-top: 0;
        }
    }
}

.l-menu__overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #565956;
    opacity: 0.56;
    z-index: 1;
    display: none;

    &.is-open {
        display: block;
    }
}
