@use "global" as *;

.c-title-block {
    letter-spacing: rem(0.96);
    text-align: center;
}

.c-title {
    font-size: rem(24);
    font-weight: $bold;
    line-height: 1.5;
    position: relative;

    &::after {
        position: absolute;
        content: "";
        bottom: rem(-16);
        left: 50%;
        transform: translateX(-50%);
        width: rem(160);
        height: rem(1);
        background: $color-font;
    }
}

.c-sub-title {
    font-family: $second-font-family;
    text-transform: uppercase;
    font-weight: $semi-bold;
    font-size: rem(24);
    margin-top: rem(40);
}
