@use "global" as *;

.p-mv {
    height: rem(742);
    position: relative;
    background: url(../images/main-visual.jpg);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;

    @include mq("md") {
        height: rem(476);
    }
}

.p-mv__content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 91%;

    @include mq("md") {
        left: rem(130);
        transform: translateY(-50%);
    }
}

.p-mv__lead {
    background: $color-base;
    font-size: rem(28);
    font-weight: $bold;
    line-height: 2.1;
    padding: rem(4) rem(24);
    letter-spacing: rem(1.12);
    display: inline;
    box-decoration-break: clone;

    @include mq("md") {
        white-space: nowrap;
        font-size: rem(40);
        letter-spacing: rem(1.6);
    }
}

.p-mv__text {
    margin-top: rem(25);
    background: $color-base;
    padding: rem(8) rem(24);
    font-weight: $bold;
    letter-spacing: rem(0.64);
    box-decoration-break: clone;
    max-width: rem(331);

    @include mq("md") {
        max-width: rem(472);
        font-size: rem(24);
        letter-spacing: rem(0.96);
        white-space: nowrap;
    }
}
