@use "global" as *;

.p-campaign {
    background: $color-main;

    @include mq("xl") {
        background: url(../images/campaign.png) no-repeat center center / cover;
    }
}

.p-campaign__wrapper {
    padding: rem(56) rem(16);

    @include mq("md") {
        padding: rem(48) rem(130);
    }
}

.p-campaign__content {
    background: $color-base;
    border-radius: rem(16);
    padding: rem(32) rem(16);
    text-align: center;
    letter-spacing: rem(0.64);

    @include mq("xl") {
        padding: rem(56) rem(64);
    }

    @include mq("xl") {
        width: 63%;
        margin: 0 auto;
    }
}

.p-campaign__text-block {
    @include mq("md") {
        position: relative;
        max-width: rem(591);
        margin: 0 auto;
    }
}

.p-campaign__label {
    background: $color-font;
    padding: 0 rem(16);
    color: $color-base;
    font-weight: $bold;

    @include mq("md") {
        font-size: rem(20);
        position: absolute;
        top: rem(-10);
        left: rem(12);
        transform: rotate(-30deg);
    }
}

.p-campaign__text {
    margin-top: rem(20);
    font-weight: $bold;

    @include mq("md") {
        font-size: rem(24);
        letter-spacing: rem(0.96);
        margin-top: rem(5);
    }
}

.p-campaign__lead {
    margin-top: rem(12);
    font-size: rem(28);
    font-weight: $bold;
    line-height: 1.5;
    letter-spacing: rem(1.12);
}

.p-campaign__items {
    margin-top: rem(28);

    @include mq("md") {
        display: flex;
        gap: rem(8);
    }
}

.p-campaign__item {
    background: $color-font;
    padding: rem(16);
    color: $color-base;
    font-weight: $bold;
    border-radius: rem(16);

    &+& {
        margin-top: rem(16);
    }

    @include mq("md") {
        flex: 1;

        &+& {
            margin-top: 0;
        }
    }

    @include mq("xl") {
        width: rem(191);
        max-width: 100%;
    }
}

.p-campaign__button-wrapper {
    margin-top: rem(30);
    display: flex;
    flex-direction: column;
    gap: rem(30);
    align-items: center;

    @include mq("md") {
        margin-top: rem(62);
        flex-direction: row;
        gap: rem(37);
        justify-content: center;
    }
}

.p-campaign__voice {
    font-size: rem(20);
    font-weight: $bold;
    letter-spacing: rem(0.8);
    background: $color-main;
    padding: rem(18) rem(40);
    position: relative;
    // width: rem(261);

    &::after {
        position: absolute;
        content: "";
        width: 0;
        height: 0;
        left: 0;
        right: 0;
        bottom: rem(-11);
        margin: auto;
        border-style: solid;
        border-color: $color-main transparent transparent transparent;
        border-width: rem(11) rem(11) 0 rem(11);

        @include mq("md") {
            border-color: transparent transparent transparent $color-main;
            border-width: rem(11) 0 rem(11) rem(11);
            bottom: 0;
            top: 50%;
            left: 100%;
            transform: translateY(-50%);
        }
    }

}

.p-campaign__button {
    color: $color-base;
    font-size: rem(20);
    font-weight: $bold;
    letter-spacing: rem(0.8);
    border-radius: rem(16);
    background: linear-gradient(87deg, #698 2.32%, #689 96.4%);
    padding: rem(25) rem(24);
    position: relative;
    transition: opacity 0.3s;
    width: 100%;
    max-width: rem(311);

    @include mq("md") {
        width: rem(233);
    }

    &::after {
        position: absolute;
        content: "";
        top: 50%;
        right: rem(16);
        transform: translateY(-50%);
        width: rem(24);
        height: rem(24);
        background: url(../images/icon-arrow.png) no-repeat center center / contain;
    }

    &:hover {
        opacity: 0.56;
    }
}
