@use "global" as *;

.p-voice__wrapper {
    padding-block: rem(63);

    @include mq("md") {
        padding-block: rem(88);
    }
}

.p-voice__items {
    margin-top: rem(75);

    @include mq("xl") {
        display: flex;
    }
}

.p-voice__item {
    &:nth-child(2n) {
        .p-voice-card__body {
            background: $color-base;
        }
    }
}

.p-voice-card {
    @include mq("xl") {
        flex: 1;
    }
}

.p-voice-card__image {
    position: relative;
}

.p-voice-card__label {
    font-family: $second-font-family;
    font-weight: $semi-bold;
    letter-spacing: rem(0.64);
    text-transform: uppercase;
    color: $color-base;
    background: $color-accent01;
    padding: rem(12) rem(16);
    position: absolute;
}

.p-voice-card__body {
    background: $color-main;
    padding: rem(24);

    @include mq("xl") {
        padding: rem(24) rem(48);
    }
}

.p-voice-card__lead {
    font-size: rem(20);
    font-weight: $bold;
    line-height: 2;
    letter-spacing: rem(0.8);
    color: $color-base;
    background: $color-accent02;
    padding: rem(4) rem(10);
    display: inline;
    box-decoration-break: clone;
}

.p-voice-card__text {
    line-height: 1.5;
    letter-spacing: rem(0.64);
    margin-top: rem(16);
}

.p-voice-card__profile {
    line-height: 1.5;
    letter-spacing: rem(0.64);
    margin-top: rem(16);

    @include mq("xl") {
        margin-top: rem(35);
    }
}
